import {
  Slider,
  Radio,
  Button,
  Space,
  Card,
  Row,
  Col,
  InputNumber,
  Input,
  Select,
  Divider,
  Layout,
  Tooltip,
} from "antd";
import { useState, useEffect } from "react";
import "./App.css";
import Paragraph from "antd/es/skeleton/Paragraph";
const actx = new AudioContext();
let osclist = [];
let mainNode = null;

export function Main_control_section() {
  function setup() {
    mainNode = actx.createGain();
    mainNode.connect(actx.destination);
    mainNode.gain.value = 0.5;
  }
  setup();
  ///play sound func
  function playTone(freq, waveform) {
    const osc = actx.createOscillator();
    osc.connect(mainNode);
    osc.type = waveformType;
    osc.frequency.value = frequency; //frequency of the key

    mainNode.gain.value = amplitude;
    //create waves, but do not start them

    osclist.push(osc);

    return osc;
  }
  function terminate_all_oscs() {
    osclist.forEach((element) => {
      element.stop();
    });
    osclist = [];
  }
  //stop all sounds
  function stop_play() {
    mainNode.gain.setValueAtTime(mainNode.gain.value, actx.currentTime);
    //console.log(release);
    //THIS IS NOT WORKING!
    mainNode.gain.linearRampToValueAtTime(0, actx.currentTime + release);
    osclist.forEach((element) => {
      element.stop(actx.currentTime + release);
    });
  }
  //#region Basic wave properties

  const [waveformType, setWaveformType] = useState("sine");
  const [amplitude, setAmplitude] = useState(1);
  const [frequency, setFrequency] = useState(440);
  const [isMouseDown, setIsMouseDown] = useState(false);
  //#region Unison waves
  const [detune, setDetune] = useState(0);

  //#endregion
  //#region ADSR envelop
  const [attack, setAttack] = useState(1);
  const [decay, setDecay] = useState(0.5);
  const [sustain, setSustain] = useState(50);
  const [release, setRelease] = useState(0.3);
  //#endregion
  //play notes, but does not stop them
  function play_notes_main() {
    if (osclist.length > 0) {
      terminate_all_oscs();
      mainNode.gain.cancelScheduledValues(actx.currentTime);
    }
    playTone(frequency, waveformType);
    //create two additional tones with detune
    if (detune > 0) {
      playTone(frequency, waveformType).detune.value = -detune;
      playTone(frequency, waveformType).detune.value = detune;
    }

    osclist.forEach((element) => {
      element.start();
    });
  }
  //play sound immediately as one of the variable changed
  useEffect(() => {
    play_notes_main();
    osclist.forEach((element) => {
      element.stop(actx.currentTime + 1);
    });
  }, [amplitude, frequency, waveformType, detune]);

  function onMouseDownMainButton() {
    play_notes_main();
    mainNode.gain.setValueAtTime(0, actx.currentTime);
    mainNode.gain.linearRampToValueAtTime(
      amplitude,
      actx.currentTime + attack
    );
    mainNode.gain.setTargetAtTime(
      (sustain / 100) * amplitude,
      actx.currentTime + attack,
      decay
    );

    setIsMouseDown(true);
  }
  function onMouseUpMainButton() {
    stop_play();
    setIsMouseDown(false);
  }
  const onKeyPressed = (e) => {
    setFrequency(e.target.value);
  };

  const onChangeWaveform = (e) => {
    setWaveformType(e.target.value);
  };
  const onChangeAmplitude = (newVal) => {
    setAmplitude(newVal);
  };
  const onChangeFrequency = (newVal) => {
    setFrequency(newVal);
  };

  const wave_options = [
    {
      label: "sine",
      value: "sine",
    },
    {
      label: "square",
      value: "square",
    },
    {
      label: "sawtooth",
      value: "sawtooth",
    },
    {
      label: "triangle",
      value: "triangle",
    },
  ];
  const common_frequency_items = [
    {
      label: "A",
      key: "A",
      value: 440,
    },
    {
      label: "A#",
      key: "A#",
      value: 466.16,
    },
    {
      label: "B",
      key: "B",
      value: 493.88,
    },
    {
      label: "C",
      key: "C",
      value: 523,
    },
    {
      label: "C#",
      key: "C#",
      value: 554,
    },
    {
      label: "D",
      key: "D",
      value: 587,
    },
    {
      label: "E",
      key: "E",
      value: 659,
    },
    {
      label: "F",
      key: "F",
      value: 698,
    },
    {
      label: "G",
      key: "G",
      value: 784,
    },
  ];
  //#endregion

  //#region React styles
  const layoutStyle = {
    borderRadius: "20px",
    overflow: "hidden",
    width: "calc(50% - 8px)",
    maxWidth: "calc(50% - 8px)",
  };
  const cardStyle = {
    textAlign: "left",
    minHeight: "40px",
    lineHeight: "20px",
    width: "420px",
    color: "#fff",
    backgroundColor: "#ffffff",
    borderRadius: "10px",
  };
  const headerStyle = {
    textAlign: "center",
    color: "#fff",
    height: 64,
    paddingInline: 48,
    lineHeight: "64px",
    backgroundColor: "#ffff00",
    borderRadius: "20px",
  };
  //#endregion
  return (
    <Layout layoutstyle={layoutStyle}>
      <Row style={headerStyle}>
        <Divider>Wave 1</Divider>
      </Row>
      <Row>
        <Col>
          <Row>
            <Card title="Main Control" size="medium" style={cardStyle}>
              <Button
                type="primary"
                onMouseDown={onMouseDownMainButton}
                onMouseUp={onMouseUpMainButton}
              >
                Hold to play Envelope
              </Button>
              <br></br>
              <Radio.Group
                value={frequency}
                buttonStyle="solid"
                options={common_frequency_items}
                optionType="button"
                style={{ marginTop: "10px" }}
                onChange={onKeyPressed}
              />
            </Card>
          </Row>
          <Row>
            <Card title="Waveform" size="medium" style={cardStyle}>
              <Radio.Group
                options={wave_options}
                onChange={onChangeWaveform}
                value={waveformType}
                optionType="button"
              />
            </Card>
          </Row>
          <Row>
            <Card title="Frequency" size="medium" style={cardStyle}>
              <Row>
                <Slider
                  min={30}
                  max={3000}
                  onChange={onChangeFrequency}
                  value={typeof frequency === "number" ? frequency : 440}
                  step={1}
                  style={{ width: "100px" }}
                />
                <InputNumber
                  min={30}
                  max={3000}
                  style={{
                    margin: "0 10px",
                  }}
                  step={10}
                  value={frequency}
                  onChange={onChangeFrequency}
                />

                <Select
                  defaultValue="A"
                  style={{
                    width: 80,
                  }}
                  onChange={onChangeFrequency}
                  options={common_frequency_items}
                />
              </Row>
            </Card>
          </Row>
          <Row>
            <Card title="Amplitude" size="medium" style={cardStyle}>
              <Row>
                <Slider
                  min={0.01}
                  max={2}
                  onChange={onChangeAmplitude}
                  value={typeof amplitude === "number" ? amplitude : 1}
                  style={{ width: "100px" }}
                  step={0.01}
                />
                <InputNumber
                  min={0}
                  max={10}
                  style={{
                    margin: "0 16px",
                  }}
                  step={0.1}
                  value={amplitude}
                  onChange={onChangeAmplitude}
                />
              </Row>
            </Card>
          </Row>
        </Col>
        <Col>
          <Card title="Unison Waves" size="medium" style={cardStyle}>
            <Row>
              <Tooltip
                title="[Try 10 cents SawTooth shape!] 
                 Detune is a parameter that is used to offset a played note from its base frequency. 
                 The unit for detune is cents; one cent is equivalent to 1/100 of a semitone and there are 12 semitones in an octave.
                 By detuning in cents rather than hertz, the percieved difference in pitch is normalized to whatever note is being detuned. 
                 A 10hz detune sounds much more dramatic on a low note with a lower frequency than a high note since it is a higher percentage of its frequency."
              >
                <InputNumber
                  addonBefore="Detune: "
                  addonAfter="cents"
                  min={0}
                  max={100}
                  step={1}
                  value={detune}
                  onChange={setDetune}
                />
              </Tooltip>
            </Row>
          </Card>
          <Card title="Envelope" size="medium" style={cardStyle}>
            <Row>
              <Tooltip title="Attack is the time taken for the rise of the level from null to peak.">
                <InputNumber
                  addonBefore="attack: "
                  addonAfter="seconds"
                  min={0}
                  max={5}
                  step={0.05}
                  value={attack}
                  onChange={setAttack}
                />
              </Tooltip>
              <Tooltip title="Decay is the time taken for the level to reduce from the attack level to the sustain level.">
                <InputNumber
                  addonBefore="decay: "
                  addonAfter="seconds"
                  min={0}
                  max={5}
                  step={0.05}
                  value={decay}
                  onChange={setDecay}
                />
              </Tooltip>
              <Tooltip title="Sustain is the level maintained until the key is released">
                <InputNumber
                  addonBefore="sustain: "
                  addonAfter="%"
                  min={0}
                  max={100}
                  step={1}
                  value={sustain}
                  onChange={setSustain}
                />
              </Tooltip>
              <Tooltip title="Release is the time taken for the level to decay to null">
                <InputNumber
                  addonBefore="release: "
                  addonAfter="seconds"
                  min={0}
                  max={5}
                  step={0.05}
                  value={release}
                  onChange={setRelease}
                />
              </Tooltip>
            </Row>
          </Card>
        </Col>
      </Row>
    </Layout>
  );
}
